.body {
  box-sizing: border-box;
  min-height: 100vh;
  text-align: center;
  background: url("pizza.5207e126.jpg") center / cover no-repeat;
  justify-content: center;
  align-items: center;
  font-family: Arial, sans-serif;
  display: flex;
}

*, :before, :after {
  box-sizing: inherit;
}

.wrapper {
  max-width: 420px;
  background: rgba(255, 255, 255, .95);
  padding: 20px;
  box-shadow: 0 0 0 10px rgba(0, 0, 0, .1);
}

h2 {
  text-align: center;
  margin: 0;
  font-weight: 200;
}

.items-list {
  text-align: left;
  margin: 0;
  padding: 0;
  list-style: none;
}

.items-list li {
  border-bottom: 1px solid rgba(0, 0, 0, .2);
  padding: 10px 0;
  font-weight: 100;
  display: flex;
}

.items-list label {
  cursor: pointer;
  flex: 1;
  line-height: 1.2;
}

.items-list input {
  display: none;
}

.items-list input + label:before {
  content: "⬜️";
  margin-right: 10px;
}

.items-list input:checked + label:before {
  content: url("check.e6b088fa.png");
}

.add-items {
  margin-top: 20px;
}

.add-items input {
  border: 1px solid rgba(0, 0, 0, .1);
  outline: 0;
  padding: 10px;
}

